.profile{
    background-color:#f1f2f4;
    margin: 0 auto;
    display: grid;
    width: 100%;
    grid-template-areas:
    "n mc" 
    ;
    grid-template-columns: 2fr 10fr;
  }
  .app_wrapper_content{
    grid-area: mc;
    background-color:#ffff;
    margin: 10px;
    border-radius: 10px;
  }
